.menu-page-image {
    background: url('./../images/menu-page.jpg');
    background-size: cover;
    background-position: center;
    padding: 11rem;
}

.menu-section-title {
    text-align: center;
    text-transform: uppercase;
    font-size: 1.7rem;
    color: #FFFFFF;
    text-shadow: 2px 2px 11px #0D0000;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    position: relative;
    bottom: 9rem;
}

.menu-list {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

    article {
        display: flex;
        flex-direction: column;
        border: 1px solid transparent;
        padding: 1rem;
        margin: 1rem 1.5rem;

        i {
            font-size: 4rem;
            color: #EF0C0C;
            margin-bottom: 0.5rem;
        }

        a {
            text-decoration: none;
            color: #333;
            background-color: lightgray;
            margin-top: 0.5rem;
            padding: 0.75rem;
            border: none;
            outline: none;
            transition: 0.1s ease-in;
        }

        a:hover,
        a:focus {
            color: #FFF;
            background-color: #EF0C0C;
        }
    }
}

@media all and (max-width: 620px) {
    .menu-list article {
        flex: 0 0 30%;
    }

    .menu-section-title {
        bottom: 6rem;
    }

    .menu-page-image {
        padding: 8rem;
    }
}