@import url('https://fonts.googleapis.com/css?family=Montserrat|Open+Sans:400,400i,700,700i');

html {
	font: 12px/1.5 "Montserrat", sans-serif;
    font-weight: 900;
}

body, form, table {
	font: inherit;
}

h1, h2, h3, h4, h5, h6 {
	font-family: "Open Sans", sans-serif;
    line-height: 1.2;
	margin: 0.75em 0 0.5em 0;
	padding: 0;
}

$headings: (h1: 2.2rem, h2: 2rem, h3: 1.8rem, h4: 1.6rem, h5: 1.4rem, h6: 1.2rem);

@each $heading, $size in $headings {
	#{$heading} { font-size: $size }
}

p {
	margin: 0;
	padding-bottom: 1.5rem;
}

ol, ul, dl {
	margin: 0 0 1.5rem 0;
}

ul, ol {
	padding: 0;
	margin-left: 1.5rem;
}

ul, ol {
	li {
		list-style-position: inside;
	}
}

ul {
	ul {
		margin-top: 0.5rem;
		margin-bottom: 0.5rem;
	}
}

ul li { list-style-type: square; }
ul ul li { list-style-type: disc; }
ul ul ul li { list-style-type: circle; }

blockquote {
	border-left: 4px solid #DDD;
	font-style: italic;
	margin: 0 0 1.5rem 0;
	padding: 1rem 2rem;
	position: relative;

	.author {
		text-align: right;
	}
}

table {
	width: 100%;
	table-layout: fixed;
	margin-bottom: 1.5rem;
	border: 1px solid #CCC;
	border-spacing: 0;
	border-collapse: collapse;

	td, th {
		border: 1px solid #CCC;
		padding: 0.3rem 0.5rem;
	}
}

table thead th {
	text-align: left;
	background: #CCC;
}

table tr:nth-child(odd) td {
	background: #F1F1F1;
}

table tr:hover td {
	background: black;
	color: #FFF;
	border-color: transparent;
}

hr {
	margin: 2rem 0;
}

/* RESPONSIVE */

@media screen and (min-width: 500px){
    html {
        font-size: 14px;
    }
}

@media screen and (min-width: 800px){
    html {
        font-size: 16px;
    }

    blockquote {
        margin: 0 2rem 1.5rem 2rem;
    }
}

@media screen and (min-width: 1300px){
    html {
        font-size: 18px;
    }
}

@media screen and (min-width: 1800px){
    html {
        font-size: 21px;
    }
}